.searchTag{
  display:inline-block;
  width: auto;
  background-color: #DCDCDC;
  margin: 15px;
  padding: 7px;
  border-radius: 35px;
  -webkit-transition: 0.1s;
  transition: 0.1s;
}
.searchTag:hover{
  cursor: pointer;
  background-color:#D8BFD8;
  -webkit-transition: 0.1s;
  transition: 0.1s;
}
  .searchTag .tagDismiss{
      color: inherit;
      border:none;
      background:none;
      margin-left: 4px;
      -webkit-transition: 0.1s;
      transition: 0.1s;
    }
  .searchTag .tagDismiss:hover{
        color: inherit;
        cursor: inherit;
        -webkit-transition: 0.1s;
        transition: 0.1s;
      }


