.download-icon{
 position:absolute;
 left: 94%;
 top: 3%;
 font-size: 1.7vmin;
 cursor:pointer;
 opacity: 0;
 color: #F0FFFF;
 }
.download-icon:hover{
   font-size: 2vmin;
   -webkit-transition: 0.5s;
   transition: 0.5s;
 }
 .search-plus-icon{
   position:absolute;
   left: 48%;
   top: 45%;
   font-size: 3vmin;
   cursor:pointer;
   }
.search-plus-icon:hover{
     font-size: 3.5vmin;
     -webkit-transition: 0.4s;
     transition: 0.4s;
   }
.result-author-avatar{
 position:absolute;
 height: 3.5vmin;
 width: 3.5vmin;
 border-radius: 50%;
 bottom: 7%;
 left: 2%;
 cursor: pointer;
 opacity: 0;
}
.result-author-image{
 position:absolute;
 bottom: 7%;
 left: 12%;
 cursor: pointer;
 opacity: 0;
}

img{
 width: 100%;
 cursor:-webkit-zoom-in;
 cursor:zoom-in;
 }

